/* ==========================================================================
   Product Exhibition shortcode styles - begin
   ========================================================================== */

.mkdf-product-exhibition {
    position: relative;
    display: flex;
	width: 100%;

    @include ipad-portrait {
        flex-direction: column !important;
    }

    $offset_label: ('no', 'minimal', 'tiny', 'small', 'normal', 'medium', 'large', 'huge' );
    $offset_value: (0, 3, 5, 10, 15, 20, 25, 40);

    @for $i from 0 to length($offset_label) {
        &.mkdf-#{nth($offset_label,$i+1)}-offset {
            $current_offset: nth($offset_value,$i+1);
            $current_offset_value: $current_offset * 2;

            &.mkdf-right-orientation {

                .mkdf-pe-background {
                    margin: 0 0 0 #{$current_offset_value}px;
                }
            }

            .mkdf-pe-background {
                margin: 0 #{$current_offset_value}px 0 0;

                @include ipad-portrait {
                    margin: 0 0 #{$current_offset_value}px !important;
                }
            }
        }
    }

    .mkdf-pe-background {
        position: relative;
        width: calc(40% - 1px);
        overflow: hidden;

        @include ipad-portrait {
            width: 100%;
            height: calc(100vh - 81px);
        }
    
        &:hover{
            .mkdf-pe-background-image-holder .mkdf-pe-background-image {
                transform: scale(1.05) translateX(5px);
            }
        }

        .mkdf-pe-background-image-holder {
            width: 100%;
            height: 100%;
            background-color: $additional-background-color;
    
            .mkdf-pe-background-image {
                @include mkdfAbsoluteHolderLayout();
                transform: scale(1.05) translateX(-5px);
                transition: .3s ease-out;
            }

            .mkdf-pe-main-image {
                display: none;

                @include ipad-portrait {
                    display: block;
                }

                img {
                    display: block;
                }
            }

            .mkdf-pe-main-info {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 55%;
                padding: 87px 30px 98px;
                text-align: center;
                background-color: $default-background-color;
                transform: translateX(-50%) translateY(-50%);
                box-sizing: border-box;

                @include phone-landscape {
                    padding: 58px 30px 68px;
                }

                @include phone-portrait {
                    padding: 28px 30px 38px;
                }

                @include smaller-phone-portrait {
                    padding: 18px 17px 28px;
                }

                .mkdf-pe-tagline {
                    margin: 0;
                }

                div.mkdf-pe-tagline {
                    font-family: $custom-font;
                    color: #e5e5e5;
                    font-size: 40px;
                    font-weight: 400;
                    line-height: 1;
                }

                .mkdf-pe-title {
                    margin: 0;
                }
                
            }
            
            .mkdf-pe-link {
                @include mkdfAbsoluteHolderLayout();
            }
        }
    }
    
    .mkdf-pe-background {
        opacity: 0;
    }

    .mkdf-pe-product-list {
        position: relative;
        width: calc(60% + 1px);

        @include ipad-portrait {
            width: 100%;
        }
    
        .mkdf-pli{
            opacity: 0;
        }
    }
}
/* ==========================================================================
   Product Exhibition shortcode styles - end
   ========================================================================== */