/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
	position: absolute;
	margin: 0 !important; //absolute centering over added item
	padding: 10px 24px;
	line-height: inherit;
	white-space: nowrap;
	border: 1px solid $default-border-color;
	border-radius: 0;
	opacity: 0;
	transform: translate(-50%, -50%);
	transform-origin: 0 0;
	
	#yith-wcwl-message {
		font-family: $default-heading-font;
		font-size: 15px;
		line-height: 1.466666666666667em;
		font-weight: 500;
		letter-spacing: .05em;
		text-transform: uppercase;
		color: $default-heading-color;
		
		&:before {
			position: relative;
			font-family: 'ElegantIcons';
			content: "\4e";
			top: 2px;
			right: 7px;
		}
	}
	
	.mkdf-qw-is-opened & {
		position: fixed;
		top: 50%;
		left: 50%;
		opacity: 1;
	}
	
	&.mkdf-wishlist-vanish-out {
		animation: mkdfWishlistvanishOut 1s both;
	}
}

@keyframes mkdfWishlistvanishOut {
	0% {
		opacity: 1;
		-webkit-transform: scale(1, 1) translate(-50%, -50%);
		-moz-transform: scale(1, 1) translate(-50%, -50%);
		transform: scale(1, 1) translate(-50%, -50%);
		-webkit-filter: blur(0px);
		filter: blur(0px);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(2, 2) translate(-50%, -50%);
		-moz-transform: scale(2, 2) translate(-50%, -50%);
		transform: scale(2, 2) translate(-50%, -50%);
		-webkit-filter: blur(10px);
		filter: blur(10px);
	}
}

.mkdf-yith-wcqv-holder {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 26px;
	margin: 0;
	line-height: 25px;
	text-align: center;
	background-color: #555555;
}

.yith-wcwl-add-button {
	transform: translateZ(0);
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {
	
	a {
		position: relative;
		display: inline-block;
		vertical-align: top;
		font-size: 0;
		line-height: inherit;
		color: $default-heading-color;
		
		&:after {
			font-family: 'Ionicons' !important;
			font-size: 16px;
			font-weight: 600;
			position: relative;
			display: inline-block;
			vertical-align: top;
			line-height: inherit;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			transition: opacity .2s;
		}
		
		&:hover {
			color: #fff;
		}
	}
}

.yith-wcwl-add-button {

	.ajax-loading {
		display: none !important;
	}

	a {

		&:after {
			content: "\f442" !important;;
		}
	}
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {
	
	.feedback {
		display: none;
	}

	a {

		&:after {
			content: "\f443" !important;;
		}
	}
}

.woocommerce-wishlist {
	
	.wishlist-title {
		display: none;
	}
	
	.woocommerce-message,
	.woocommerce-info,
	.woocommerce-error {
		position: relative;
		display: block;
		padding: 15px 30px;
		margin: 50px 0 30px;
		line-height: 48px;
		list-style: none;
		background-color: transparent;
		border: 1px solid $default-border-color;
		
		> a {
			color: $default-text-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
		
		a {
			float: right;
		}
	}
	
	table.wishlist_table {
		margin: 50px 0 100px;
		font-size: inherit;

		@include phone-landscape {

			th,
			td {

				&.product-name {
					width: 30%;
				}

				&.product-thumbnail,
				&.product-stock-status {
					display: none;
				}
			}
		}

		@include phone-portrait {

			th,
			td {

				&.product-remove {
					width: 20px;
				}

				&.product-name {
					width: 25%;
					padding-right: 10px;
				}

				&.product-price {
					width: 20%;
				}
			}
		}

		thead {

			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;

				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					@include mkdfDefaultHeadingStyle();
					@include mkdfH5();
					margin: 0;
					border: 0;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				td {
					padding: 25px 0;
					vertical-align: middle;
					text-align: inherit;
					
					&.product-remove {
						padding-left: 10px;
						padding-right: 10px;

						@include phone-portrait {
							padding-left: 5px;
							padding-right: 5px;
						}
						
						a {
							color: $default-text-color;
							font-size: 0;
							line-height: 1;
							
							&:after {
								content: "\f404";
								display: inline-block;
								vertical-align: middle;
								speak: none;
								color: inherit;
								font-family: "Ionicons";
								font-size: 25px;
								line-height: inherit;
								font-style: normal;
								font-weight: normal;
								font-variant: normal;
								text-transform: none;
								text-rendering: auto;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
							}

							&:hover {
								color: $default-heading-color;
							}
						}
					}
					
					&.product-thumbnail {
						width: 86px;
						padding-right: 10px;

						a, img {
							display: block;
						}
					}
					
					&.product-name {

						.yith-wcqv-button {
							height: 38px;
							line-height: 38px;
							padding: 0 28px;
							margin: 0;
						}
					}
					
					&.product-price {
						@include mkdfHeadingStyle(h5);
						
						ins {
							text-decoration: none;
						}
					}
					
					&.product-stock-status span {
						color: $default-text-color !important;
					}
					
					&.product-add-to-cart {
						text-align: right;

						a {
							display: inline-block !important;
							@include mkdfButtonDefaultStyle();
					        @include mkdfButtonSize();
					        @include mkdfButtonSolidColor();

							@include ipad-portrait {
								padding: 11px 30px;
							}

							@include phone-portrait {
								padding: 11px 10px;
							}
						}
					}
				}
			}
		}
		
		.yith-wcwl-share {
			margin: 0;
			display: none;
		}
	}
}

.mkdf-single-product-summary,
#yith-quick-view-content {
	
	.yith-wcwl-add-to-wishlist {
		width: auto;
		margin: 50px 0 0;
		background-color: transparent;
		
		.yith-wcwl-add-button,
		.yith-wcwl-wishlistaddedbrowse,
		.yith-wcwl-wishlistexistsbrowse {
			
			a {
				font-family: $default-text-font;
				font-size: 18px;
				font-style: italic;
				line-height: 24px;
				color: $default-heading-color;
				
				&:after {
					float: left;
					padding: 0 7px 0 0;
					font-size: 27px;
					font-weight: 500;
					font-style: normal;
					line-height: 24px;
				}

				&:hover {
					color: $default-text-color;
				}
			}
		}
		
		.yith-wcwl-add-button {
			
			a {
				
				&:after {
					content: "\f442";
				}
			}
		}

		.yith-wcwl-wishlistaddedbrowse,
		.yith-wcwl-wishlistexistsbrowse {

			a {

				&:after {
					content: "\f443";
				}
			}
		}
	}
}

.mkdf-wishlist-widget-holder {
	display: inline-block;
	vertical-align: middle;
	
	a {
		position: relative;
		top: 1px;
		margin: 0 10px;
		font-size: 22px;
		color: $default-heading-color;
		
		&:hover {
			color: $default-text-color;
		}

		.mkdf-wishlist-widget-count {
			position: relative;
			top: -1px;
			margin-left: 4px;
			font-family: $default-heading-font;
			font-size: 14px;
			letter-spacing: .1em;
		}
	}

	.mkdf-sticky-header & {

		a {
			color: #fff;

			&:hover {
				color: #fff;
			}
		}
	}
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */