/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */

#yith-quick-view-modal,
.yith-quick-view.yith-modal {
	
	.yith-quick-view-overlay {
		background-color: rgba(255, 255, 255, .75);
	}
	
	.yith-wcqv-wrapper {
		padding: 0;
		max-width: 950px;
		height: auto !important;

		@include laptop-landscape-mac {
			max-width: 900px;
		}

		@include laptop-landscape-medium {
			max-width: 950px;
		}

		@include ipad-portrait {
			max-width: 600px;
		}
	}
	
	.yith-wcqv-main {
		@include mkdfRelativeHolderLayout();
		padding: 0;
		overflow: hidden !important;
		background-color: #fff;
		box-shadow: 0 0 18px rgba(#000, .1);
		box-sizing: border-box;
	}
	
	#yith-quick-view-content {
		
		.woocommerce-product-gallery__wrapper {
			margin: 0 -6px;
			
			div {
				padding: 0 6px;
				box-sizing: border-box;
				
				&:not(:first-child) {
					position: relative;
					display: inline-block;
					vertical-align: top;
					width: 25%;
					float: left;
				}
			}
		}
		
		.product {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0;
		}

		.product-type-external {
			
			.summary {
				
				.mkdf-single-product-share-wish {
					margin-top: 47px;
				}
			}
		}
		
		.product-type-variable,
		.product-type-grouped {

			.summary {

				form.cart {
					margin: 37px 0 0;

					.select2-container--default {
						float: none;
					}

					.single_variation_wrap {
						margin: 5px 0 0;
					}
				}
			}
		}
		
		.images,
		.summary {
			position: relative;
			float: left;
			box-sizing: border-box;
		}
		
		.images {
			width: 50%;
			
			a, img {
				display: block;
			}
		}
		
		.summary {
			width: 50%;
			padding: 65px 44px 70px;

			.variations {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 0 20px;
				padding: 0;
				border: none;

				select {
					position: relative;
					top: -3px;
					display: inline-block;
					vertical-align: middle;
					padding: 0 16px 0 16px;
					margin: 0 -4px 0 0;
					height: 50px;
					font-family: $default-text-font;
					font-size: 18px;
					line-height: 50px;
					font-weight: inherit;
					color: $default-text-color;
					background-color: #fff;
					border: 1px solid #e1e1e1;
					box-sizing: border-box;
				}

				tr {
		            margin: 0 0 10px;
		            padding: 0 0 20px;
		            border: 0;

		            &:last-child {
		                margin: 0;
		            }
		        }
				
				th {
					
					&.label {
						border-right: none;
					}
				}

		        td {
		            padding: 0 0 13px;
		            text-align: initial;

			        &.label {
				        padding-right: 10px;
			        }

			        &.value {
				        white-space: nowrap;

		                select {
		                    width: auto;
		                    min-width: 200px;
		                    margin: 0;
		                }

		                .select2-container--default {
		                    margin: -3px 0 0;
		                }
			        }
		        }

		        .reset_variations {
			        position: relative;
			        top: -1px;
			        margin: 0 0 0 15px;
		            color: $default-heading-color;
		        }
			}

			.single_variation_wrap {
				margin: 35px 0 0;

				.single_variation {
					margin: 0 0 35px;
				}
			}

			table.group_table {
		        position: relative;
		        display: inline-block;
		        vertical-align: middle;
		        margin: 0;
		        border: none;
		        padding: 0;

		        tbody {
		            display: block;
		        }

		        tr {
		            display: block;
		            margin: 0;
		            padding: 0;
		            border: 0;
		        }

		        td {
		            padding: 0 17px 20px 0;
		            text-align: left;

		            &.price {
		                display: table-cell;
		                vertical-align: middle;
		            }

		            &:last-child {
		                padding: 0 0 10px;
		            }
		        }

		        .mkdf-quantity-buttons {
		            margin: 0;
		        }

		        a {
		            color: $default-text-color;

		            &:hover {
		                color: $default-heading-color;
		            }
		        }
		    }
			
			.mkdf-yith-product-title {
				display: block;
		        margin: 0 0 7px;
			}
			
			div[itemprop="offers"] {
				position: relative;
				display: block;
			}
			
			.price {
				@include mkdfWooCommercePriceStyle();
			}
			
			.woocommerce-product-rating {
				@include mkdfRelativeHolderLayout();
			    line-height: 1;
			    margin: 13px 0 3px;
			}
			
			div[itemprop="description"],
			.woocommerce-product-details__short-description {
				@include mkdfRelativeHolderLayout();
			    margin: 17px 0 0;

			    p {
				    margin: 0;
			    }
			}

			.woocommerce-product-details__short-description {

			    p {
				    margin: 0 60px 0 0;
			    }
			}
			
			form.cart {
				@include mkdfRelativeHolderLayout();
		        margin: 43px 0 0;
			}

			.mkdf-quantity-buttons {
			    margin: 0 27px 0 0;

				@include phone-portrait {
					margin: 0 10% 0 0;
				}
		    }
			
			.mkdf-single-product-share-wish {
				position: relative;
				display: inline-block;
				width: 100%;
				vertical-align: middle;
				margin: 0;
				
				.yith-wcwl-wishlistexistsbrowse,
				.yith-wcwl-wishlistaddedbrowse {
					
					a {
						
						&:after {
							color: $first-main-color;
						}
					}
				}
				
				.clear {
					display: none;
				}
				
				> div {
					padding: 0 20px;
					
					&:after {
						content: "|";
						position: absolute;
						left: 0;
						top: 0;
						color: #d3d3d3;
					}
					
					&:first-child {
						padding-left: 0;
						
						&:after {
							display: none;
						}
					}
					
					&:last-child {
						padding-right: 0;
					}
				}
				
				.mkdf-woo-social-share-holder {
					position: relative;
					float: left;
					
					> * {
						display: table-cell;
						vertical-align: top;
						line-height: 22px;
					}
					
					> span {
						font-size: 12px;
						text-transform: uppercase;
						letter-spacing: 2px;
						color: $first-main-color;
						padding: 0 9px 0 0;
					}
				}
			}

			.yith-wcwl-add-to-wishlist {
				margin: 40px 0 0;
			}

			p.stock.out-of-stock,
			p.stock.in-stock {
				margin: 0 0 18px;
                font-weight: 700;
                color: $default-heading-color;
			}
		}
	}
	
	#yith-quick-view-close {
		top: 26px;
		right: 35px;
		font-size: 0;
		border: 0;
		opacity: 1;
		color: $default-heading-color;

		@include ipad-portrait {
			top: 2px;
			right: 11px;
		}

		svg {
			fill: currentColor;
		}

		&:hover {
			color: $first-main-color;
		}
	}
}

/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */