/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
	position: relative;
	display: inline-block;
	vertical-align: top;
	height: 100%;
	margin: 0 10px;
	
	.touchevents & {
		cursor: pointer;
	}
	
	&:hover {
		
		.mkdf-sc-dropdown {
			max-height: 100vh;
			pointer-events: auto;
			transition: .35s cubic-bezier(.66,.14,.58,1) .1s;
		}
	}
	
	.mkdf-shopping-cart-inner {
		@include mkdfRelativeHolderLayout();
		height: 100%;
	}
	
	.mkdf-header-cart {
		position: relative;
		display: flex;
		align-items: center;
		height: 100%;
		
		&:hover{
			color: initial;
		}

		.mkdf-sticky-header & {
	        color: #fff;

	        &:hover{
	            color: #fff;
	        }

			.mkdf-sc-opener-count {
				color: #fff;
			}
	    }
		
		.mkdf-sc-opener-holder {
			position: relative;
			padding-right: 10px;
		}
		
		.mkdf-sc-opener-icon {
			position: relative;
			display: inline-block;
			vertical-align: top;
			line-height: 1;
		}
		
		.mkdf-sc-opener-count {
			position: absolute;
			bottom: -7px;
			right: 0;
			font-family: $default-heading-font;
			font-size: 12px;
			color: $default-heading-color;
		}
		
		&.mkdf-header-cart-icon-pack {
			
			.mkdf-sc-opener-icon {
				font-size: 25px;
				
				* {
					display: block;
					line-height: inherit;
					
					&:before {
						display: block;
						line-height: inherit;
					}
				}
			}
		}
		
		&.mkdf-header-cart-svg-path {
			
			.mkdf-sc-opener-icon {
				
				> * {
					vertical-align: middle;
				}
				
				> *,
				path {
					fill: currentColor;
					//stroke: currentColor;
				}
			}
		}
	}
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
	color: $header-dark-color;
	
	&:hover {
		color: $header-dark-hover-color;
	}
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
	color: $header-light-color;
	
	&:hover {
		color: $header-light-hover-color;
	}
}

.mkdf-sc-dropdown {
	position: absolute;
	top: 100%;
	left: auto;
	right: 0;
	text-align: left;
	max-height: 0;
	overflow: hidden;
	box-sizing: border-box;
	transition: 0.35s;
	
	.mkdf-sc-dropdown-inner {
		@include mkdfRelativeHolderLayout();
		width: 300px;
		padding: 46px 36px;
		background-color: $default-heading-color;
		border: 1px solid $default-heading-color;
		box-sizing: border-box;
	}
	
	.mkdf-empty-cart {
		color: #cfcfcf;
	}
	
	.mkdf-sc-dropdown-items {
		@include mkdfRelativeHolderLayout();
	}
	
	.mkdf-sc-dropdown-item {
		position: relative;
		display: flex;
		align-items: flex-start;
		margin-top: 24px;
		
		&:first-child {
			margin-top: 0;
		}
		
		a {
			&:hover {
				color: #fff;
			}
		}
	}
	
	.mkdf-sc-dropdown-item-image {
		flex-shrink: 0;
		width: 58px;
		margin-right: 20px;
		
		a, img {
			display: block;
		}
	}
	
	.mkdf-sc-dropdown-item-content {
		@include mkdfRelativeHolderLayout();
		padding-right: 20px;
		
		> * {
			color: #cfcfcf;
			font-style: italic;
			font-size: 22px;
		}
	}
	
	.mkdf-sc-dropdown-item-title {
		margin: 0;
	}
	
	.mkdf-sc-dropdown-item-quantity-price {
		margin: 2px 0 0;
	}
	
	.mkdf-sc-dropdown-item-remove {
		position: absolute;
		top: -2px;
		right: 0;
		font-size: 20px;
		line-height: 1;
	}
	
	.mkdf-sc-dropdown-subtotal {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 20px;
		
		> * {
			margin: 0;
		}
		
		.mkdf-sc-dropdown-total {
			color: #cfcfcf;
		}
		
		.mkdf-sc-dropdown-total-amount {
			color: #cfcfcf;
			font-style: italic;
			font-size: 22px;
		}
	}
	
	.mkdf-sc-dropdown-button-holder {
		@include mkdfRelativeHolderLayout();
		margin-top: 10px;
		
		.mkdf-sc-dropdown-button {
			@include mkdfButtonDefaultStyle();
			@include mkdfButtonSize(huge);
			@include mkdfButtonSolidColor();
			
			&.mkdf-cart,
			&.mkdf-checkout {
				border: 1px solid $default-background-color;
				padding: 7px 0;
				font-size: 8px;
			}
			
			&:hover {
				color: $default-heading-color;
				background-color: #fff;
				border-color: #fff;
			}
		}
	}
}

.mkdf-vertical-menu-area {
	
	.mkdf-sc-dropdown {
		display: none !important;
	}
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */