/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 6px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 6px 0 0 6px;
                }
            }
        }
    }

    &.widget_rating_filter {

        .mkdf-widget-title {
            margin: 0 0 18px;
        }

        .star-rating {
            vertical-align: text-top;
        }
    }

    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include mkdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkdf-widget-title {
            margin: 0 0 9px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .mkdf-widget-title {
            margin: 0 0 9px;
        }
    }
    
    &.widget_price_filter {

        .mkdf-widget-title {
            margin: 0 0 15px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: $additional-background-color;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 9px;
                    width: 9px;
                    background-color: $default-heading-color;
                    margin: 0 0 0 -1px;
                    border-radius: 100%;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        transform: translateX(-50%);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: #ffe7e3;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 30px 0 0;
            
            .button {
                float: right;
                padding: 10px 36px;
                font-size: 9px;
            }
            
            .price_label {
                float: left;
                font-style: normal;
                line-height: 36px;
            }
        }
    }

    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories {

        ul {

            li {
                margin-bottom: 6px;
                color: $default-heading-color;

                a {
                    font-style: italic;
                    font-size: 18px;
                    color: inherit;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }
    }

    &.widget_product_categories {

        .mkdf-widget-title {
            margin: 0 0 11px;
        }
    }

    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .mkdf-widget-title {
            margin: 0 0 20px;
        }

        ul {

            li {
                margin-bottom: 26px;

                &:last-child {
                    margin-bottom: 0;
                }

                & >:not(a) {
                    float: none;
                    margin: 0;
                }

                a {

                    img {
                        top: auto;
                        left: auto;
                    }
                }
            }
        }

        .mkdf-woo-widget-img {

		    a, img {
			    position: relative;
		    }

            a {
                display: inline-block;
                padding: 0;
            }

		    img {
			    max-width: 110px;
                display: block;
		    }
	    }

        .mkdf-woo-widget-content {

            .mkdf-woo-widget-title {
                display: block;
                margin: 2px 0 0;
                font-family: $default-text-font;
                font-size: 16px;
                color: $default-heading-color;

                a {
                    padding: 0;
                    color: inherit;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }

            .star-rating {
                margin: 6px 0 5px;
            }

            .mkdf-woo-widget-price {
                @include mkdfWooCommercePriceStyle();
                margin: 0;
                line-height: 20px;

                del,
                ins,
                .amount {
                    display: inline-block;
                }

                del .amount {
                    text-decoration: line-through;
                }
            }

            .reviewer {
                display: block;
                margin: 8px 0 0;
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {

        .mkdf-widget-title {
            margin: 0 0 11px;
        }
        
        .tagcloud {

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 14px 4px 0;
                font-family: inherit;
                font-size: 16px !important;
                line-height: inherit;
                font-weight: inherit;
                letter-spacing: inherit;
                text-transform: capitalize;
                color: $default-text-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
    
    &.widget_product_search {

        .mkdf-widget-title {
            margin: 0 0 20px;
        }

        .woocommerce-product-search {
            @include mkdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include mkdfRelativeHolderLayout();
            }
            
            input,
            button {
                height: 52px;
				padding: 14px 0;
				font-family: inherit;
				font-weight: inherit;
				line-height: 24px;
				border-radius: 0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
            }
            
            input {
                @include mkdfRelativeHolderLayout();
				padding-left: 20px;
				padding-right: 60px;
				font-size: 18px;
				font-style: italic;
				color: $default-text-color;
				background-color: transparent;
				border: 1px solid $default-border-color;
				transition: border-color .2s ease-out;

				&:focus {
					color: $default-heading-color;
					background-color: transparent;
					border-color: $first-main-color;
				}

				@include mkdfPlaceholder {
					color: inherit;
				}
            }
            
            button {
                position: absolute;
				top: 0;
				right: 0;
				width: 50px;
				font-size: 25px;
				text-align: center;
				color: $default-heading-color;
				background-color: transparent;
				border: 0;
				cursor: pointer;
				transition: color .2s ease-out;

				span {
					display: block;
					line-height: inherit;

					&:before {
						display: block;
						line-height: inherit;
					}
				}

				&:hover {
					color: $default-text-color;
				}
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */