/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
	@include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
	margin: 0 0 22px;
	
	.mkdf-woo-pl-info-on-image-hover & {
		margin: 0 0 50px;
	}
}

ul.products {
	list-style: none;
	margin: 0 -15px;
	clear: both;
	
	> .product {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		float: left;
		margin: 0 0 30px;
		padding: 0 15px;
		box-sizing: border-box;
		
		&.outofstock {
			
			.mkdf-pl-inner {
				
				.mkdf-pl-text-inner {
					
					.button:not(.yith-wcqv-button) {
						display: none;
					}
				}
			}
		}
		
		.mkdf-product-list-title {
			margin: 0;
		}
		
		.mkdf-product-categories {
			margin: 2px 0 0;
			
			a {
				position: relative;
				font-family: $default-text-font;
				font-size: 16px;
				color: $default-text-color;
				z-index: 1;
				
				&:hover {
					color: $first-main-color;
				}
			}
		}
		
		.mkdf-pl-rating-holder {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 11px 0 2px;
		}
		
		.price {
			@include mkdfWooCommercePriceStyle();
		}
		
		.mkdf-pl-action-buttons-holder {
			margin: 40px 0 0;
		}
		
		.button,
		.added_to_cart {
			margin: 0 9px 0 0;
			padding: 0 !important;
		}
		
		.yith-wcwl-add-to-wishlist {
			margin: 0;
			padding: 0;
		}
		
		.mkdf-pl-text-wrapper {
			@include mkdfRelativeHolderLayout();
			margin: 27px 0 36px;
			text-align: center;
			
			@include ipad-landscape {
				padding-bottom: 35px;
			}
		}
		
		.variations_form.cart {
			position: relative;
			display: inline-block;

			/* Variations removed from shop default lists */
			display: none !important;

			vertical-align: top;
			margin: 18px 0 0;
			
			.variations {
				margin: 0;
			}
			
			.select_option.select_option_colorpicker {
				border-radius: 50%;
				
				.yith_wccl_value {
					width: 7px;
					height: 7px;
					min-width: auto;
					border: 0;
					border-radius: 50%;
				}
			}
			
			.select_option {
				margin-right: 4px;
				border: none;
				
				&:last-child {
					margin-right: 0;
				}
				
				.yith_wccl_value {
					height: auto;
					border: 0;
				}
			}
		}
		
		.mkdf-pl-inner {
			@include mkdfRelativeHolderLayout();
			overflow: hidden;
			
			.touchevents & {
				cursor: pointer;
			}
			
			&:hover {
				
				.mkdf-pl-text {
					opacity: 1;
				}
			}
			
			.mkdf-pl-image {
				@include mkdfRelativeHolderLayout();
				overflow: hidden;
				
				img {
					display: block;
					width: 100%;
				}
			}
			
			.mkdf-pl-text {
				@include mkdfAbsoluteHolderLayout();
				padding: 10px;
				opacity: 0;
				background-color: $additional-background-color;
				box-sizing: border-box;
				transition: opacity .35s ease-out;
			}
			
			.mkdf-pl-text-outer {
				@include mkdfTableLayout();
				text-align: center;
			}
			
			.mkdf-pl-text-inner {
				@include mkdfTableCellLayout();
				
				.mkdf-pl-text-action {
					display: table;
					margin: 0 auto;
					text-align: center;
					
					.yith-wcqv-button {
						
						.mkdf-ms-explorer & {
							
							&:after{
								position: relative;
								left: 3px;
							}
						}
					}
				}
				
				.clear {
					display: none;
				}
				
				.button.added {
					display: none;
				}
				
				.button,
				.added_to_cart,
				.yith-wcwl-add-to-wishlist {
					font-size: 0;
					display: inline-block;
					height: 37px;
					width: 37px;
					line-height: 37px;
					text-align: center;
					color: $default-heading-color;
					background-color: transparent;
					border: 1px solid #d7d7d7;
					transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
					box-sizing: border-box;
					z-index: 3;
					
					&:after {
						font-family: 'Ionicons';
					}
					
					&:hover {
						color: #fff;
						background-color: $default-heading-color;
						border-color: $default-heading-color;
						transition-delay: 0s;
					}
				}
				
				.yith-wcwl-add-to-wishlist {
					a {
						display: block;
					}
				}
				
				.button.product_type_external,
				.button.product_type_grouped,
				.add_to_cart_button {
					
					&:after {
						content: "+";
						position: relative;
						top: -2px;
						font-family: $default-heading-font;
						font-size: 22px;
					}
				}
				
				.added_to_cart {
					
					&:after {
						content: "\f370";
						font-size: 20px;
					}
				}
				
				.yith-wcqv-button {
					
					&:after {
						content: "\f133";
						font-size: 24px;
					}
				}
			}
			
			> a {
				@include mkdfAbsoluteHolderLayout();
			}
		}
	}
}

$columns_space_label: ('no', 'minimal', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 3, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
	&.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
		$column_width: nth($columns_space_width, $i+1);
		$column_width_margin: $column_width * 2;
		
		ul.products {
			@if ($column_width == 0) {
				margin: 0;
			} @else {
				margin: 0 -#{$column_width}px;
			}
			
			> .product {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
	
	$product_list_columns: ('columns-3', 'columns-4');
	
	@for $i from 0 to length($product_list_columns) {
		$column_width: 100%/($i+3);
		
		> .products {
			
			> .product {
				
				@media only screen and (min-width: $ipad-landscape-plus-pixel) {
					
					.mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
						width: $column_width;
						
						&:nth-child(#{$i+3}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
}

.woocommerce {
	
	&.columns-1 {
		
		> .products {
			
			> .product {
				float: none;
				width: 100%;
			}
		}
	}
	
	$product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
	
	@for $i from 0 to length($product_list_vc_columns) {
		&.#{nth($product_list_vc_columns,$i+1)} {
			$column_vc_width: 100%/($i+2);
			
			> .products {
				
				> .product {
					width: $column_vc_width;
					$product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
					
					@if $product_list_vc_columns_label == 'columns-5' {
						@media only screen and (min-width: $laptop-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-6' {
						@media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-3' {
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-2' {
						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else {
						@media only screen and (min-width: $ipad-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
}

.mkdf-woo-pl-info-on-image-hover {
	margin: 0 0 50px;
	
	ul.products>.product {
		.mkdf-pl-inner {
			.mkdf-product-list-title,
			.mkdf-product-categories,
			.price {
				transform: translateY(10px) translateZ(0);
				transition: .4s ease-out;
				transition-delay: .1s;
			}
			
			&:hover {
				.mkdf-product-list-title,
				.mkdf-product-categories,
				.price {
					transform: translateY(0) translateZ(0);
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */