/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page {
	
	table.cart {

		@include phone-landscape {

			th,
			td {

				&.product-thumbnail {
					display: none;
				}
			}
		}

		@include phone-portrait {

			th,
			td {

				&.product-remove {
					width: 20px;
				}
			}
		}

		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					@include mkdfDefaultHeadingStyle();
					@include mkdfH5();
					margin: 0;

					&.product-remove {

						@include phone-portrait {
							width: 30px;
						}
					}
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;

					@include phone-portrait {
						padding-left: 5px;
						padding-right: 5px;
					}
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
						width: 100%;
					}
				}

				&.product-price,
				&.product-subtotal {
					@include mkdfHeadingStyle(h5);
				}

				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 43px 0 20px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				
				label {
					display: none;
				}
				
				.input-text {
					padding: 10px 48px;
				}
				
				.button {
					top: -3px;
				}
			}
			
			> input,
			> button {
				float: right;
			}
			
			.button {
				margin: 0 0 0 22px;
			}
		}
	}
	
	.cart-collaterals {
		@include mkdfRelativeHolderLayout;
		margin: 0 0 50px;

		strong {
			font-weight: 400;
		}

		h2 {
			position: relative;
			display: block;
			margin: 53px 0 26px;
		}
		
		table {
			margin: 0 0 40px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 20%;
				padding-right: 5px;
				border: 0;
				@include mkdfDefaultHeadingStyle();
				@include mkdfH5();
				margin: 0;
			}
		}
		
		tr {
			
			&.shipping {
				
				#shipping_method {
					list-style: none;
					margin: 0 0 7px;
					
					li {
						margin: 3px 0;
						padding: 0;
						
						> * {
							display: inline-block;
							vertical-align: middle;
							margin: 0;
						}
					}
				}
			}

			&.cart-subtotal,
			&.order-total {
				td {
					@include mkdfHeadingStyle(h5);
					margin: 0;

					strong {
						color: $default-heading-color;
					}
				}
			}
			
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 10px;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}
	
	.cart_totals {
		margin: 1px 0 20px;
	}
}

.mkdf-woocommerce-page {
	
	.mkdf-custom-cart-holder {
		text-align: left;
		padding: 13px 0 12px 0;
		
		.cart-empty.woocommerce-info {
			border: 0;
			padding: 0 0 4px;
			margin: 0;
		}

		.mkdf-empty-cart-custom {
			position: absolute;
			bottom: 212px;

			@include phone-landscape {
			    bottom: 251px;
			}
		}
	}
	
	.cart-empty {
		position: relative;
		display: block;
		font-size: 30px;
		text-transform: uppercase;
		font-family: $default-heading-font;
		color: $default-heading-color;
		margin: 0 0 20px;
	}
	
	.mkdf-empty-cart-text {
		font-style: italic;
		color: $additional-heading-color;
		margin: 0;
		padding-bottom: 21px;
	}

	.mkdf-empty-cart-custom {
		width: max-content;
		margin: 10px 0 -2px;
		font-family: $custom-font;
		color: $custom-text-color;
		font-size: 40px;
		z-index: -1;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		margin: 0 0 60px;
		
		a.button {
			color: $default-heading-color !important;
			background-color: transparent !important;
			border: 1px solid $default-heading-color !important;
			
			&:hover {
				color: #fff !important;
				background-color: $default-heading-color !important;
				border-color: $default-heading-color !important;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */