/* ==========================================================================
   Qode WishList For WooCommerce - Add Wishlist Button styles - begin
   ========================================================================== */

%wishlist-loop-styles {
	display: inline-block;
	vertical-align: middle;
	margin: 0;
	padding: 0;

	.qwfw-shortcode {

		&:not(.button) {
			height: 37px;
			width: 37px;
			justify-content: center;
			color: var(--qwfw-atw-color, $default-heading-color);
			background-color: var(--qwfw-atw-background-color, transparent);
			border: 1px solid #d7d7d7;
			transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;

			.qwfw-m-icon {
				width: var(--qwfw-atw-icon-size, 15px);
			}

			.qwfw-spinner-icon {
				color: currentColor;
			}

			&:hover {
				color: var(--qwfw-atw-hover-color, #fff);
				background-color: var(--qwfw-atw-hover-background-color, var(--qwfw-atw-background-color, $default-heading-color));
				border-color: $default-heading-color;
			}

			&.qwfw--added {
				color: var(--qwfw-atw-active-color, $default-heading-color);

				&:hover {
					color: var(--qwfw-atw-hover-color, #fff);
					background-color: var(--qwfw-atw-hover-background-color, var(--qwfw-atw-background-color, $default-heading-color));
					border-color: $default-heading-color;
				}
			}
		}
	}
}

.qwfw-add-to-wishlist-wrapper {

	.qwfw-shortcode {

		&:not(.button) {
			color: var(--qwfw-atw-color, $default-heading-color);

			&:hover {
				color: var(--qwfw-atw-hover-color, $default-text-color);
			}

			&.qwfw--added {
				color: var(--qwfw-atw-active-color, $default-heading-color);

				&:hover {
					color: var(--qwfw-atw-active-color, var(--qwfw-atw-hover-color, $default-text-color));
				}
			}
		}
	}

	&.qwfw--loop {

		@at-root .mkdf-pl-inner #{&} {
			@extend %wishlist-loop-styles;
		}

		@at-root .mkdf-pli-inner #{&} {
			@extend %wishlist-loop-styles;
		}

		@at-root .mkdf-plc-inner #{&} {
			@extend %wishlist-loop-styles;
		}
	}

	&.qwfw--single {

		.qwfw-shortcode {

			&:not(.button) {
				gap: var(--qwfw-atw-gap, 8px);

				.qwfw-m-icon {
					width: var(--qwfw-atw-icon-size, 25px);

					&.qwfw--predefined {

						svg {
							stroke-width: 1px;
						}
					}
				}

				.qwfw-m-text {
					font-family: $default-text-font;
					font-size: 18px;
					font-style: italic;
					line-height: 24px;
				}
			}
		}

		&.qwfw-position--after-add-to-cart {
			margin-top: 50px;
		}
	}
}

/* ==========================================================================
   Qode WishList For WooCommerce - Wishlist Page styles - begin
   ========================================================================== */

.qwfw-wishlist-page {

	.woocommerce-message,
	.woocommerce-info,
	.woocommerce-error {
		position: relative;
		display: block;
		padding: 15px 30px;
		margin: 50px 0 30px;
		line-height: 48px;
		list-style: none;
		background-color: transparent;
		border: 1px solid $default-border-color;

		> a {
			color: $default-text-color;

			&:hover {
				color: $default-heading-color;
			}
		}

		a {
			float: right;
		}
	}
}

.qwfw-wishlist-table {
	margin: 50px 0 100px;

	.qwfw-m-section-heading {
		margin-bottom: 0;
	}

	.qwfw-m-items.shop_table {
		font-size: inherit;

		thead {
			background-color: var(--qwfw-wt-heading-bg-color, transparent);

			tr {
				border: 0;
				border-bottom: 1px solid var(--qwfw-wt-border-color, $default-border-color);

				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					@include mkdfDefaultHeadingStyle();
					@include mkdfH5();
					margin: 0;
					border: 0;
				}
			}
		}

		tbody {

			tr {
				border: 0;
				border-bottom: 1px solid var(--qwfw-wt-border-color, $default-border-color);

				td {
					padding: 25px 0;
					vertical-align: middle;
					text-align: inherit;

					&.product-remove {
						padding-left: 10px;
						padding-right: 10px;

						@include phone-portrait {
							padding-left: 5px;
							padding-right: 5px;
						}

						a {
							color: $default-text-color;

							.qwfw-e-remove-button-icon svg {
								width: 10px;
							}

							&:hover {
								color: $default-heading-color;
							}
						}
					}

					&.product-thumbnail {
						padding-right: 10px;

						a {
							max-width: 86px;

							@include ipad-portrait {
								max-width: 70px;
							}
						}
					}

					&.product-price {
						@include mkdfHeadingStyle(h5);

						ins {
							text-decoration: none;
						}
					}

					&.product-stock-status span {
						color: $default-text-color !important;
					}

					&.product-add-to-cart {
						text-align: right;

						.qwfw-e-add-to-cart {
							display: inline-block !important;
							@include mkdfButtonDefaultStyle();
							@include mkdfButtonSize();
							@include mkdfButtonSolidColor();

							@include ipad-portrait {
								padding: 11px 18px;
								font-size: 11px;
							}

							@include phone-portrait {
								padding: 11px 10px;
							}

							&:hover {
								@include mkdfButtonSolidHoverColor();
							}
						}
					}
				}
			}
		}
	}

	.qwfw-social-share .qwfw-m-social-item {

		a {
			color: var(--qwfw-wt-social-icon-color, $default-heading-color);

			&:hover {
				color: var(--qwfw-wt-social-icon-hover-color, $default-text-color);
			}
		}
	}
}
