/* ==========================================================================
   QODE Quick View styles - begin
   ========================================================================== */
.qqvfw-quick-view-button-wrapper {

	@at-root .mkdf-pl-inner #{&}, .mkdf-pli-inner #{&}, .mkdf-plc-inner #{&} {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		padding: 0;

		.qqvfw-quick-view-button {

			&.qqvfw-shortcode {

				&.button {
					gap: 0;
					justify-content: center;

					.qqvfw-m-icon {
						width: 24px;

						svg {
							fill: currentColor;
							stroke: none;
						}
					}
				}
			}
		}
	}
}

#qode-quick-view-for-woocommerce-pop-up {
	
	.qqvfw-m-overlay {
		background-color: rgba(255, 255, 255, .75);
	}
	
	.qqvfw-m-content {
		padding: 0;
		width: auto;
		max-width: 950px;

		@include laptop-landscape-mac {
			max-width: 900px;
		}

		@include laptop-landscape-medium {
			max-width: 950px;
		}

		@include ipad-portrait {
			max-width: 600px;
		}

		@include phone-landscape {
			max-width: 90vw;
		}
	}

	.qqvfw-m-content {

		.qqvfw-m-product {

			> .product {
				margin: 0;
				box-shadow: 0 0 18px rgba(#000, .1);
			}

			> .product-type-external {

				.summary {

					.mkdf-single-product-share-wish {
						margin-top: 47px;
					}
				}
			}

			> .product-type-variable,
			> .product-type-grouped {

				.summary {

					form.cart {
						margin: 37px 0 0;

						.select2-container--default {
							float: none;
						}

						.single_variation_wrap {
							margin: 5px 0 0;
						}
					}
				}
			}
		}
		
		.qqvfw-m-media-wrapper {
			width: 50%;
			
			a, img {
				display: block;
			}

			img {
				height: 100%;
				object-fit: cover;
			}

			a.woocommerce-product-gallery__trigger {
				display: none;
			}

			.woocommerce-product-gallery__wrapper {

				> div {
					&:not(:first-child) {
						display: none;
					}
				}
			}
		}
		
		.qqvfw-m-summary-wrapper {
			width: 50%;

			.summary {
				padding: 65px 44px 70px;
			}

			.variations {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 0 20px;
				padding: 0;
				border: none;

				select {
					position: relative;
					top: -3px;
					display: inline-block;
					vertical-align: middle;
					padding: 0 16px 0 16px;
					margin: 0 -4px 0 0;
					height: 50px;
					font-family: $default-text-font;
					font-size: 18px;
					line-height: 50px;
					font-weight: inherit;
					color: $default-text-color;
					background-color: #fff;
					border: 1px solid #e1e1e1;
					box-sizing: border-box;
				}

				tr {
		            margin: 0 0 10px;
		            padding: 0 0 20px;
		            border: 0;

		            &:last-child {
		                margin: 0;
		            }
		        }
				
				th {
					
					&.label {
						border-right: none;
					}
				}

		        td {
		            padding: 0 0 13px;
		            text-align: initial;

			        &.label {
				        padding-right: 10px;
			        }

			        &.value {
				        white-space: nowrap;

		                select {
		                    width: auto;
		                    min-width: 200px;
		                    margin: 0;
		                }

		                .select2-container--default {
		                    margin: -3px 0 0;
		                }
			        }
		        }

		        .reset_variations {
			        position: relative;
			        top: -1px;
			        margin: 0 0 0 15px;
		            color: $default-heading-color;
		        }
			}

			.single_variation_wrap {
				margin: 35px 0 0;

				.single_variation {
					margin: 0 0 35px;
				}
			}

			table.group_table {
		        position: relative;
		        display: inline-block;
		        vertical-align: middle;
		        margin: 0;
		        border: none;
		        padding: 0;

		        tbody {
		            display: block;
		        }

		        tr {
		            display: block;
		            margin: 0;
		            padding: 0;
		            border: 0;
		        }

		        td {
		            padding: 0 17px 20px 0;
		            text-align: left;

		            &.price {
		                display: table-cell;
		                vertical-align: middle;
		            }

		            &:last-child {
		                padding: 0 0 10px;
		            }
		        }

		        .mkdf-quantity-buttons {
		            margin: 0;
		        }

		        a {
		            color: $default-text-color;

		            &:hover {
		                color: $default-heading-color;
		            }
		        }
		    }
			
			.mkdf-qode-quick-view-product-title {
				display: block;
		        margin: 0 0 7px;
			}
			
			div[itemprop="offers"] {
				position: relative;
				display: block;
			}
			
			.price {
				@include mkdfWooCommercePriceStyle();
			}
			
			.woocommerce-product-rating {
				@include mkdfRelativeHolderLayout();
			    line-height: 1;
			    margin: 13px 0 3px;
			}
			
			div[itemprop="description"],
			.woocommerce-product-details__short-description {
				@include mkdfRelativeHolderLayout();
			    margin: 17px 0 0;

			    p {
				    margin: 0;
			    }
			}

			.woocommerce-product-details__short-description {

			    p {
				    margin: 0 60px 0 0;
			    }
			}
			
			form.cart {
				@include mkdfRelativeHolderLayout();
		        margin: 43px 0 0;
			}

			.mkdf-quantity-buttons {
			    margin: 0 27px 0 0;

				@include phone-portrait {
					margin: 0 10% 0 0;
				}
		    }
			
			.mkdf-single-product-share-wish {
				position: relative;
				display: inline-block;
				width: 100%;
				vertical-align: middle;
				margin: 0;
				
				.yith-wcwl-wishlistexistsbrowse,
				.yith-wcwl-wishlistaddedbrowse {
					
					a {
						
						&:after {
							color: $first-main-color;
						}
					}
				}
				
				.clear {
					display: none;
				}
				
				> div {
					padding: 0 20px;
					
					&:after {
						content: "|";
						position: absolute;
						left: 0;
						top: 0;
						color: #d3d3d3;
					}
					
					&:first-child {
						padding-left: 0;
						
						&:after {
							display: none;
						}
					}
					
					&:last-child {
						padding-right: 0;
					}
				}
				
				.mkdf-woo-social-share-holder {
					position: relative;
					float: left;
					
					> * {
						display: table-cell;
						vertical-align: top;
						line-height: 22px;
					}
					
					> span {
						font-size: 12px;
						text-transform: uppercase;
						letter-spacing: 2px;
						color: $first-main-color;
						padding: 0 9px 0 0;
					}
				}
			}

			.qwfw-add-to-wishlist-wrapper {
				margin: 40px 0 0;
			}

			p.stock.out-of-stock,
			p.stock.in-stock {
				margin: 0 0 18px;
                font-weight: 700;
                color: $default-heading-color;
			}
		}
	}
	
	.qqvfw-m-close {
		top: 26px;
		right: 35px;
		font-size: 0;
		border: 0;
		opacity: 1;
		color: var(--qqvfw-qv-close-button-color, $default-heading-color);

		@include ipad-portrait {
			top: 2px;
			right: 11px;
		}

		svg {
			fill: currentColor;
		}

		&:hover {
			color: var(--qqvfw-qv-close-button-hover-color, $first-main-color);
		}
	}
}

/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */