/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.mkdf-plc-holder {
    @include mkdfRelativeHolderLayout();

    &.mkdf-info-on-image {

		.mkdf-plc-inner {

			.mkdf-plc-text-inner {

				.mkdf-plc-text-action {
					margin-top: 40px;
					margin-bottom: 4px;
				}
			}
		}
	}

	&.mkdf-nav-predefined-arrow {
		
		.mkdf-owl-slider {
			
			.owl-nav {
				
				.owl-next,
				.owl-prev {
					transform: translateY(calc(-50% - 88px));
					
					.mkdf-ms-explorer & {
						top: calc(50% - 88px);
					}
				}
			}
		}
	}

	&.mkdf-nav-outside-slider {

		.mkdf-owl-slider {

			.owl-nav {

				.owl-prev {
	                left: -75px;

					@include laptop-landscape-large {
						left: -58px;
					}
	            }

	            .owl-next {
	                right: -75px;

		            @include laptop-landscape-large {
			            right: -58px;
		            }
	            }
			}
		}
	}

    .mkdf-plc {
        padding: 0;
        z-index: 100;

	    .mkdf-plc-title {
		    margin: 0;
	    }

	    .mkdf-plc-category {
		    margin: 2px 0 0;

		    a {
                position: relative;
                font-family: $default-text-font;
                font-size: 16px;
                color: $default-text-color;
                z-index: 1;

                &:hover {
                    color: $first-main-color;
                }
            }
	    }

	    .mkdf-plc-rating-holder {
		    @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 11px 3px 2px;
	    }

	    .mkdf-plc-price {
	        @include mkdfWooCommercePriceStyle();
        }

	    .mkdf-plc-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

	    .mkdf-plc-excerpt {
            margin: 1px 0 3px;
		    font-size: 19px;
		    line-height: 25px;
		    font-style: italic;
		    color: #5f5f5f;
        }

	    .button,
        .added_to_cart {
            margin: 0 9px 0 0;
            padding: 0;
        }

        .yith-wcwl-add-to-wishlist {
            margin: 0;
            padding: 0;
        }
    }

    .mkdf-plc-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-plc-text {
                opacity: 1;
            }
        }

        .touchevents & {
            cursor: pointer;
        }

        .mkdf-plc-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .mkdf-plc-onsale,
            .mkdf-plc-out-of-stock,
            .mkdf-plc-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-plc-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-plc-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }

	        .mkdf-plc-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }

        .mkdf-plc-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
	        transition: opacity .35s ease-out;
	        background-color: $additional-background-color;
        }

        .mkdf-plc-text-outer {
	        @include mkdfTableLayout();
            text-align: center;
        }

        .mkdf-plc-text-inner {
            @include mkdfTableCellLayout();

	        .mkdf-plc-text-action {
                display: table;
                margin: 0 auto;
		
		        .yith-wcqv-button {
			
			        .mkdf-safari & {
				        display: flex;
				
				        &:after{
					        position: relative;
					        left: -1px;
				        }
			        }
		        }
            }

            .clear{
                display: none;
            }

            .button.added {
                display: none;
            }

            .button,
            .added_to_cart,
            .yith-wcwl-add-to-wishlist {
	            position: relative;
                font-size: 0;
                display: inline-block;
                float: left;
                height: 37px;
                width: 37px;
                line-height: 37px;
                text-align: center;
                color: $default-heading-color;
                background-color: transparent;
                border: 1px solid #d7d7d7;
                transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
	            box-sizing: border-box;
                z-index: 3;

                &:after {
                    font-family: 'Ionicons';
                }

                &:hover{
                    color: #fff;
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
	                transition-delay: 0s;
                }
            }

            .yith-wcwl-add-to-wishlist {

                a {
                    display: block;
                }
            }

            .button.product_type_external,
            .button.product_type_grouped,
            .add_to_cart_button {

                &:after{
                    content: "+";
                    position: relative;
                    top: -2px;
                    font-family: $default-heading-font;
                    font-size: 22px;
                }
            }

            .added_to_cart{

                &:after{
                    content: "\f370";
                    font-size: 20px;
                }
            }

            .yith-wcqv-button {

                &:after{
                    content: "\f133";
                    font-size: 24px;
                }


            }
        }

	    .mkdf-plc-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-plc-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 27px 0 46px;
        text-align: center;
    }

	&.mkdf-product-info-light {

		.mkdf-plc-inner {

			.mkdf-plc-text-inner {

				.mkdf-plc-title,
				.mkdf-plc-category a,
				.mkdf-plc-excerpt,
				.mkdf-plc-rating,
				.mkdf-plc-price {
					color: #fff;
				}

				.mkdf-plc-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}

	&.mkdf-light-skin {

		.mkdf-plc-inner {

			.mkdf-plc-text-inner {

				.button,
	            .added_to_cart,
	            .yith-wcwl-add-to-wishlist {
	                color: $default-heading-color;
	                background-color: #fff;
	                border: 1px solid #fff;

	                &:hover{
	                    color: #fff;
	                    background-color: $default-heading-color;
	                    border-color: #d7d7d7;
	                }
	            }
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */