/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
	@include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				.mkdf-pli-text-action {
					margin-top: 40px;
					margin-bottom: 4px;
				}
			}
		}
	}
	
	.mkdf-pli {
		padding: 0;
		z-index: 100;
		
		.mkdf-pli-title {
			margin: 0;
		}
		
		.mkdf-pli-category {
			margin: 2px 0 0;
			
			a {
				position: relative;
				font-family: $default-text-font;
				font-size: 16px;
				color: $default-text-color;
				z-index: 1;
				
				&:hover {
					color: $first-main-color;
				}
			}
		}
		
		.mkdf-pli-rating-holder {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 11px 0 2px;
		}
		
		.mkdf-pli-price {
			@include mkdfWooCommercePriceStyle();
		}
		
		.mkdf-pli-rating {
			@include mkdfWooCommerceRatingsStyle();
		}
		
		.mkdf-pli-excerpt {
			margin: 1px 0 3px;
			font-size: 19px;
			line-height: 25px;
			font-style: italic;
			color: #5f5f5f;
		}
		
		.button,
		.added_to_cart {
			margin: 0 9px 0 0;
			padding: 0;
		}
		
		.yith-wcwl-add-to-wishlist {
			margin: 0;
			padding: 0;
		}
	}
	
	.mkdf-pli-inner {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
		
		&:hover {
			
			.mkdf-pli-text {
				opacity: 1;
			}
		}
		
		.touchevents & {
			cursor: pointer;
		}
		
		.mkdf-pli-image {
			@include mkdfRelativeHolderLayout();
			
			img {
				display: block;
				width: 100%;
			}
			
			.mkdf-pli-onsale,
			.mkdf-pli-out-of-stock,
			.mkdf-pli-new-product {
				@include mkdfWooCommerceMarksGeneralStyle();
			}
			
			.mkdf-pli-onsale {
				@include mkdfWooCommerceSaleStyle();
			}
			
			.mkdf-pli-out-of-stock {
				@include mkdfWooCommerceOutOfStockStyle();
			}
			
			.mkdf-pli-new-product {
				@include mkdfWooCommerceNewProductStyle();
			}
		}
		
		.mkdf-pli-text {
			@include mkdfAbsoluteHolderLayout();
			padding: 10px;
			opacity: 0;
			box-sizing: border-box;
			transition: opacity .35s ease-out;
			background-color: $additional-background-color;
		}
		
		.mkdf-pli-text-outer {
			@include mkdfTableLayout();
			text-align: center;
		}
		
		.mkdf-pli-text-inner {
			@include mkdfTableCellLayout();
			
			.mkdf-pli-text-action {
				display: table;
				margin: 0 auto;
				
				.yith-wcqv-button {
					
					.mkdf-safari & {
						display: flex;
						
						&:after{
							position: relative;
							left: -1px;
						}
					}
				}
			}
			
			.clear {
				display: none;
			}
			
			.button.added {
				display: none;
			}
			
			.button,
			.added_to_cart,
			.yith-wcwl-add-to-wishlist {
				position: relative;
				font-size: 0;
				display: inline-block;
				float: left;
				height: 37px;
				width: 37px;
				line-height: 37px;
				text-align: center;
				color: $default-heading-color;
				background-color: transparent;
				border: 1px solid #d7d7d7;
				transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
				box-sizing: border-box;
				z-index: 3;
				
				&:after {
					font-family: 'Ionicons';
				}
				
				&:hover {
					color: #fff;
					background-color: $default-heading-color;
					border-color: $default-heading-color;
					transition-delay: 0s;
				}
			}
			
			.yith-wcwl-add-to-wishlist {
				
				a {
					display: block;
				}
			}
			
			.button.product_type_external,
			.button.product_type_grouped,
			.add_to_cart_button {
				
				&:after {
					content: "+";
					position: relative;
					top: -2px;
					font-family: $default-heading-font;
					font-size: 22px;
				}
			}
			
			.added_to_cart {
				
				&:after {
					content: "\f370";
					font-size: 20px;
				}
			}
			
			.yith-wcqv-button {
				
				&:after {
					content: "\f133";
					font-size: 24px;
				}
				
			}
		}
		
		.mkdf-pli-link {
			@include mkdfAbsoluteHolderLayout();
		}
	}
	
	.mkdf-pli-text-wrapper {
		@include mkdfRelativeHolderLayout();
		margin: 27px 0 36px;
		text-align: center;
	}
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-light-skin {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.button,
				.added_to_cart,
				.yith-wcwl-add-to-wishlist {
					color: $default-heading-color;
					background-color: #fff;
					border: 1px solid #fff;
					
					&:hover {
						color: #fff;
						background-color: $default-heading-color;
						border-color: #d7d7d7;
					}
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */