/* ==========================================================================
   Product List - Simple shortcode styles - begin
   ========================================================================== */

.mkdf-pls-holder {
	@include mkdfRelativeHolderLayout();

	.mkdf-pls-outer {
		@include mkdfRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;

		.mkdf-pls-outer {
			@include mkdfRelativeHolderLayout();
			padding: 0;
			margin: 0 0 45px;

			&:last-child {
				margin: 0;
			}
		}
	}
	
	.mkdf-pls-image {
		display: table-cell;
		vertical-align: middle;
		width: 70px;

		a, img {
			display: block;
			max-width: 70px;
		}

		a {
			position: relative;
		}
	}
	
	.mkdf-pls-text {
		display: table-cell;
		vertical-align: middle;
		padding: 0 0 0 10px;
		
		.mkdf-pls-title {
			margin: 0;
		}

		.mkdf-pls-excerpt {
			color: $default-text-color;
	        line-height: 1.3em;
            margin: 1px 0 7px;
		}

		.mkdf-pls-price {
			@include mkdfWooCommercePriceStyle();
		}

		.mkdf-pls-rating-holder {
			@include mkdfRelativeHolderLayout();
			line-height: 1;
			margin: 5px 0 3px;
		}
		
		.mkdf-pls-rating {
			@include mkdfWooCommerceRatingsStyle();
		}
	}
}
/* ==========================================================================
   Product List - Simple shortcode styles - end
   ========================================================================== */