/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {

				@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {
					@include mkdfWooCommerceSingleProductThumbsOnLeftSideStyles();
				}
				@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-mac) {
					@include mkdfWooCommerceSingleProductThumbsOnLeftSideStyles(104);
				}
				@media only screen and (min-width: $laptop-landscape-mac-plus-pixel) {
					@include mkdfWooCommerceSingleProductThumbsOnLeftSideStyles(124);
				}
			}
		}
	}

	&.mkdf-firefox,
	&.mkdf-edge {

		.woocommerce-tabs {

			#reviews {

				.comment-respond {

					.stars {

						a.active::after {
							bottom: 4px;
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 21px 0 50px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 51%;
		    margin: 0;
		    padding: 0 23px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    .woocommerce-product-gallery__wrapper {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        padding: 0 0 0 23px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 7px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 13px 0 3px;

		    .woocommerce-review-link {
			    position: relative;
			    top: 2px;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 27px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 37px 0 0;
		    font-family: $default-heading-font;
		    font-size: 12px;
		    line-height: 24px;
		    letter-spacing: .2em;
		    text-transform: uppercase;
		    color: $default-heading-color;
		
		    > span {
			    display: block;
			    margin: 1px 0;
			
			    a {
				    color: $default-heading-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    margin: 54px 0 0;

		    .mkdf-quantity-buttons {
			    margin: 0 20px 0 0;

			    @include phone-portrait {
				    margin: 0 10% 0 0;
			    }
		    }
	    }
    }

	.product {

		&.product-type-variable,
		&.product-type-grouped {

			form.cart {
				margin: 55px 0 0;
			}
		}
	}

	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 119px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 2px 0 0;
				
				@include phone-portrait {
					display: block;
					margin: 0 8px 8px 0;
				}

				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 12px 59px 16px;
					font-family: $default-text-font;
					font-size: 20px;
					font-style: italic;
					color: $additional-heading-color;
					border: 1px solid #d7d7d7;
					transition: border-color .2s ease-out;
				}

				&:last-of-type {
					margin: 0;
				}

				&.active,
				&:hover {

					a {
						border-color: $default-heading-color;
					}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 24px 0 20px;
			}
		}
		
		table {
			margin: 24px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				@include mkdfHeadingStyle(h6);
				line-height: 23px;
			}
			
			td {
				width: 99%;
				line-height: 23px;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 19px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}

			.woocommerce-noreviews {
				margin: 0;
			}

			.comment-notes {
				margin: 10px 0 7px;
			}

			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 7px;
				
				.comment-reply-title {
					display: inline-block;
					margin: 0 0 7px;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						vertical-align: top;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;

		> h2 {
			display: block;
			margin: 77px 0 34px;
			font-size: 24px;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */