/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 25px 19px;
        margin: 0 0 30px;
        line-height: 38px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a.button,
        a.wc-forward:not(.added_to_cart):not(.checkout-button) {
            top: -7px;
            float: right;
            
            &:hover {
                @include mkdfButtonOutlineColor()
            }
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */