/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .mkdf-onsale,
    .mkdf-sold,
    .mkdf-new-product {
        @include mkdfWooCommerceMarksGeneralStyle();
    }
    
    .mkdf-onsale {
        @include mkdfWooCommerceSaleStyle();
    }
    
    .mkdf-sold {
        @include mkdfWooCommerceOutOfStockStyle();
    }

    .mkdf-new-product {
        @include mkdfWooCommerceNewProductStyle();
    }
}

.single-product,
.mkdf-woo-single-page {
    
    .mkdf-single-product-content {

        .mkdf-onsale,
        .mkdf-sold,
        .mkdf-new-product {
            right: 10px;
        }
    }
    
    &.mkdf-woo-single-thumb-on-left-side {
        
        .mkdf-single-product-content {

            .mkdf-onsale,
            .mkdf-sold,
            .mkdf-new-product {
                right: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */