/* ==========================================================================
   WooCommerce Track Order page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page.mkdf-woocommerce-order-tracking {
	
	.woocommerce {
		margin: 0 0 45px;
		
		> .track_order {
			display: block;
			margin: 0 0 60px;
			
			> p:not(.form-row) {
				display: block;
				margin: 0 0 7px;
			}
			
			.form-row-first,
			.form-row-last {
				@include mkdfRelativeHolderLayout();
				margin: 0;
				
				label {
					display: block;
					margin: 9px 0;
					color: $default-heading-color;
					font-size: 18px;
					line-height: 38px;
				}
			}
			
			.button {
				margin: 13px 0 0;
			}
		}
		
		.mkdf-empty-cart-custom {
			margin: 0 0 0 10px;
		}
		
		h2.mkdf-empty-cart-title {
			margin: 0 0 3px;
		}
		
		.mkdf-empty-cart-text {
			padding-bottom: 26px;
		}
		
		.woocommerce-form {
			
			.form-row-first,
			.form-row-last {
				font-family: $default-heading-font;
				letter-spacing: 0.2em;
				font-size: 12px;
				text-transform: uppercase;
				
				label {
					margin-bottom: 0;
					font-size: 12px;
				}
			}
		}
		
	}
}

/* ==========================================================================
   WooCommerce Track Order page styles - end
   ========================================================================== */