/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: relative;
            float: left;
            width: 52px;
            height: 52px;
            margin: 0 -1px 0 0;
            font-family: $default-heading-font;
            font-size: 18px;
            font-style: normal;
            font-weight: 300;
            line-height: 48px;
            text-align: center;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid #d8d8d8;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            padding: 0;
            outline: 0;
            cursor: text;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            cursor: pointer;
            transition: color .15s ease-in-out;

            &:hover {
                color: $first-main-color;
            }
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */