/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.mkdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize();
        @include mkdfButtonSolidColor();
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include mkdfButtonSolidHoverColor();
        }

        &.qodef-search-form-button {
            align-items: center;
            display: inline-flex;
            flex-shrink: 0;
            padding: 0 1em;

            &.qodef--has-icon {
                @include qodefButtonSearchIconStyle();
                height: initial;
                padding: 0;
            }

            // position
            &.qodef--button-outside {
                margin-left: 1em;
                border: 1px solid #000;

                svg {
                    fill: currentColor;
                }
            }

            &.qodef--button-inside {
                background-color: transparent !important;
                border: none !important;
                height: 100%;
                position: absolute;
                right: 0;
                top: 0;
            }
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */
