/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {
	
	&.mkdf-ms-explorer {
		
		.woocommerce-ordering .select2 .select2-selection.select2-selection--single {
			padding: 12px 50px;
			
			.select2-selection__rendered {
				position: relative;
				left: -70px;
			}
		}
	}
	
	.mkdf-pl-controls-holder {
		display: flex;
	}
	
	.woocommerce-result-count,
	.woocommerce-ordering {
		display: inline;
		width: 50%;
		margin: 0;
		float: left;
		font-family: $default-heading-font;
		font-size: 12px;
		text-transform: uppercase;
		letter-spacing: .2em;
		color: $default-heading-color;
	}
	
	.woocommerce-result-count {
		line-height: #{$sselect-input-height}px;
	}
	
	.woocommerce-ordering {
		text-align: right;
		
		.orderby {
			width: auto;
			min-width: 280px;
			margin: 0;
			text-align: initial;
		}
		
		.select2 {
			margin-bottom: 0;
			text-align: initial;
			
			.select2-selection.select2-selection--single {
				padding: 14px 15px;
				font-family: inherit;
				font-size: inherit;
				font-style: normal;
				color: inherit;
				background-color: transparent;
				border: none;
				
				.select2-selection__arrow {
					width: 22px;
					
					&:before {
						content: "\f3d0";
						font-family: "Ionicons";
						font-size: 22px;
						font-weight: 500;
					}
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */