/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 31px;
    width: auto;
    height: 33px;
    margin: 0;
    padding: 0 40px;
    color: $default-heading-color;
    font-size: 20px;
    font-style: italic;
    line-height: 33px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
    right: 0;
    background-color: #feeae7;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: #feeae7;
}

@mixin mkdfWooCommerceNewProductStyle() {
    right: 0;
    background-color: #feeae7;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle($font-size: 22px) {
    position: relative;
    display: block;
    margin: 4px 0 0;
    font-family: $default-text-font;
    font-size: $font-size;
    font-weight: 500;
    line-height: 1;
    letter-spacing: .05em;
    text-transform: uppercase;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 15px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: 5px;
    color: #c4c4c4;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f4b2\f4b2\f4b2\f4b2\f4b2";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f4b2";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        position: absolute;
        right: -4px;
        bottom: 5px;
        display: block;
        white-space: nowrap;
        @extend %star-ratings;
        letter-spacing: 8.8px;
        pointer-events: none;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */

@mixin mkdfWooCommerceSingleProductThumbsOnLeftSideStyles($thumb_size: 151) { // 153

    .woocommerce-product-gallery__wrapper {
        width: 100%;
        margin: 0;
        overflow: hidden;
    }

    $space_between_items: 10;

    .woocommerce-product-gallery__image {

        &:first-child {
            $first_thumb_offset: $thumb_size + $space_between_items;

            left: #{$first_thumb_offset}px;
            width: calc(100% - #{$first_thumb_offset}px);
        }
    }

    &.woocommerce-product-gallery--with-images {

        .woocommerce-product-gallery__image {

            &:not(:first-child) {
                position: absolute;
                top: 0;
                left: 0;
                width: #{$thumb_size}px;
                padding: 0 0 #{$space_between_items}px;
                box-sizing: border-box;

                a {
                    height: 100%;

                    img {
                        height: 100%;
                    }
                }
            }
        }
    }

    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
    @for $i from 0 to length($thumb_3_columns_label) {
        &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
            $column_value: $i+2;
            $column_size: 100 / $column_value;
            $column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);

            .woocommerce-product-gallery__image {

                &:not(:first-child) {
                    height: $column_width;
                }

                $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                @for $i from 0 to length($thumbnail_numbers) {
                    $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                    &:nth-child(#{$thumbnail_number_value}#{n}) {
                        $first_param: ($column_size * $i)#{'%'};
                        $second_param: ($space_between_items / $column_value * $i)#{px};
                        $topPos: calc(#{$first_param} + #{$second_param});

                        top: $topPos;
                    }
                }
            }
        }
    }
}