/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.mkdf-woocommerce-page .mkdf-content {
    
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tr {
            margin: 0 0 10px;
            padding: 0 0 20px;
            border: 0;
            
            &:last-child {
                margin: 0;
            }
        }
    
        th {
        
            &.label {
                border-right: none;
            }
        }
        
        td {
            padding: 0 0 13px;
            text-align: initial;
	        
	        &.label {
		        padding-right: 10px;
	        }
	        
	        &.value {
		        white-space: nowrap;
                
                select {
                    width: auto;
                    min-width: 200px;
                    margin: 0;
                }
        
                .select2-container--default {
                    margin: -3px 0 0;
                }
	        }
        }
        
        .reset_variations {
	        margin: 0 0 0 15px;
            color: $default-heading-color;
            @include mkdfButtonDefaultStyle();
            padding-left: 15px;
            
            &:before {
                content: '';
                position: relative;
                bottom: 4px;
                width: 11px;
                height: 1px;
                display: inline-block;
                margin-right: 10px;
                background-color: currentColor;
            }
            
            &:hover {
                color: $first-main-color;
            }
        }
    }
    
    .single_variation_wrap {
        margin: 35px 0 0;
        
        .single_variation {
            margin: 0 0 35px;
        }
    }
    
    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tbody {
            display: block;
        }

        tr {
            display: block;
            margin: 0;
            padding: 0;
            border: 0;
            
            .woocommerce-grouped-product-list-item__label,
            .woocommerce-grouped-product-list-item__price {
                font-size: 18px;
                font-style: italic;
                padding-right: 16px;
            }
        }
        
        td {
            padding: 0 1px 20px 0;
            text-align: left;
            
            @include phone-portrait {
                display: block;
                padding: 0 1px 8px 0;
            }
            
            &.price {
                display: table-cell;
                vertical-align: middle;
            }
            
            &:last-child {
                padding: 0 0 10px;
                
                @include phone-portrait {
                    padding: 0 0 23px;
                }
            }
        }
        
        .mkdf-quantity-buttons {
            margin: 0;
        }
        
        a {
            color: $default-text-color;
            
            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */