/* ==========================================================================
   Product Info shortcode styles - begin
   ========================================================================== */

.mkdf-product-info {
    position: relative;
	display: inline-block;
	vertical-align: middle;

	> div {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		
		p {
			margin: 0;
		}

		a {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin: 0 9px 0 0;

			&:last-child {
				margin: 0;
			}
		}
	}

	.mkdf-pi-title,
	.mkdf-pi-image,
	.mkdf-pi-rating {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}
	
	.mkdf-pi-image {
		
		img {
			display: block;
		}
	}
	
	.mkdf-pi-category {
		
		h1, h2, h3, h4, h5, h6 {
			margin: 0;
			
			a {
				vertical-align: top;
			}
		}
	}
	
	.mkdf-pi-rating {
		@include mkdfWooCommerceRatingsStyle();
	}
	
	.mkdf-pi-add-to-cart {

		.button.added {
            display: none;
        }

        .button,
        .added_to_cart {
            font-size: 0;
            display: inline-block;
            float: left;
            height: 37px;
            width: 37px;
            line-height: 37px;
            text-align: center;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid #d7d7d7;
            transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
            box-sizing: border-box;
            z-index: 3;

            &:after {
                font-family: 'Ionicons';
            }

            &:hover{
                color: #fff;
                background-color: $default-heading-color;
                border-color: $default-heading-color;
            }

	        &.mkdf-light-skin {

		        color: $default-heading-color;
                background-color: #fff;
                border: 1px solid #fff;

                &:hover{
                    color: #fff;
                    background-color: $default-heading-color;
                    border-color: #d7d7d7;
                }
	        }
        }

        .button.product_type_external,
        .button.product_type_grouped,
        .add_to_cart_button {

            &:after{
                content: "+";
                position: relative;
                top: -2px;
                font-family: $default-heading-font;
                font-size: 22px;
            }
        }

        .added_to_cart {

            &:after {
                content: "\f370";
                font-size: 20px;
            }
        }
	}
}
/* ==========================================================================
   Product Info shortcode styles - end
   ========================================================================== */